.register{
    width: 600px;
    height: 500px;
    background-color: #ffff;
    border :1 px solid #ffff;
    box-shadow : 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb (0 0 0 / 10%);
    border-radius:  20 px ;
    padding :1rem;
    align-items : center;
    text-align : center;

}
.register >input{
    border-radius:  8px;
    border : 2px solid #dddfe2;
    outline :none;
    color : #1d2129;
    margin: 0.5rem 0 ;
    padding : 0.5rem 0.75rem;
    width: 92%;
    font-size: 1rem;
}
.register >.button {
    background: blue;
    border: 1px solid #1877f2;
    color :#ffff;
    font-size:1.25rem;
    padding: 0.5rem;
    margin:0.5rem 0 ;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
    
}
.checkbox-wrapper {
    padding:  4%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    
}
.button:hover {
    background-color: rgb(30, 0, 139);
  }